import React from "react";
import "./pagination.css";

function Pagination({ dogsByPage, countDogs, paginate }) {
	const pageNumbers = [];
	for (let i = 1; i <= Math.ceil(countDogs / dogsByPage); i++) {
		pageNumbers.push(i);
	}

	return (
		<div className="Pagination">
			{pageNumbers.map((e) => (
				<p key={e} className="pagNum">
					<button
						onClick={() => paginate(e)}
						className="pageLink"
					>
						{e}
					</button>
				</p>
			))}
		</div>
	);
}

export default Pagination;
