import {
	GET_ALL_DOGS,
	GET_BY_ID,
	GET_BY_NAME,
	GET_TEMPS,
	FILTER,
} from "./actions";

let initialState = {
	allDogs: [],
	dog: [],
	temperaments: [],
	filteredDogs: [],
};

const Actions = (state = initialState, action) => {
	switch (action.type) {
		case GET_BY_ID:
			return {
				...state,
				dog: action.payload,
			};
		case GET_BY_NAME:
			return {
				...state,
				allDogs: action.payload,
			};

		case GET_ALL_DOGS:
			return {
				...state,
				allDogs: action.payload,
			};

		case GET_TEMPS:
			return {
				...state,
				temperaments: action.payload,
			};

		case FILTER:
			const { name, breed, temperament, sort } = action.payload;
			let filtered = state.AllDogs;
			if (name) {
				filtered = filtered.filter((e) =>
					e.name.toLowerCase().includes(name.toLowerCase())
				);
			}
			if (breed) filtered = filtered.filter((e) => e.breed === breed);

			if (temperament) {
				filtered = filtered.filter((e2) =>
					e2.temperament.split(", ").includes(temperament)
				);
			}
			switch (sort) {
				case "nameAsc":
					filtered = filtered.sort((a, b) => {
						if (a.name > b.name) return 1;
						if (a.name < b.name) return -1;
						return 0;
					});
					break;
				case "nameDesc":
					filtered = filtered.sort((b, a) => {
						if (a.name > b.name) return 1;
						if (a.name < b.name) return -1;
						return 0;
					});
					break;
				case "weightAsc":
					filtered = filtered.sort((a, b) => {
						if (a.weight > b.weight) return 1;
						if (a.weight < b.weight) return -1;
						return 0;
					});
					break;
				case "weightDsc":
					filtered = filtered.sort((a, b) => {
						if (a.weight > b.weight) return 1;
						if (a.weight < b.weight) return -1;
						return 0;
					});
					break;
				default:
					break;
			}

			return {
				...state,
				filteredDogs: filtered,
			};

		default:
			return state;
	}
};

export default Actions;
