import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Cards from "../Cards/Cards";
import {
	getTemperaments,
	filter,
	getAllDogs,
	getByName,
} from "../../redux/actions";
import Pagination from "../Pag/pagination";
import "./HomePage.css";

const HomePage = () => {
	const dispatch = useDispatch();
	let allDogs = useSelector((state) => state.allDogs); //Global dogs

	let [filter, setFilter] = useState({
		name: "",
		breed: "",
		temperament: "",
		sort: "",
	}); // Local filter

	//Evento xra todos los filtros y busquedas
	const handleChange = (e) => {
		e.preventDefault();
		setFilter({ ...filter, [e.target.name]: e.target.value });
	};

	//breeds
	var breeds = [];
	allDogs.map((e) => breeds.push(e.name));

	//Temperaments
	let alltemp = useSelector((state) => state.temperaments); //Global Temperaments
	var temps = alltemp.sort((a, b) => {
		if (a.name > b.name) return 1;
		return 0;
	});

	/////////////////////////PAGINADO
	const [pag, setpag] = useState({
		currentpage: 1,
		dogsByPage: 8,
	}); //Local

	const indexOfLastDog = pag.currentpage * pag.dogsByPage; // 4*8 = 32)
	const indexOfFirstDog = indexOfLastDog - pag.dogsByPage; // 32-8 = 24
	const currentDogs = allDogs.slice(indexOfFirstDog, indexOfLastDog); //24 - 32;

	const paginate = (pageNumber) =>
		setpag({
			...pag,
			currentpage: pageNumber,
		});

	useEffect(() => {
		dispatch(getAllDogs());
		dispatch(getTemperaments());
		//console.log(dogs);
	}, []);

	return (
		<div className="HomePage">
			<div className="Display-Filters">
				{/* [ ] Input de búsqueda para encontrar razas de perros por nombre*/}
				<div className="Finder-By-Name">
					<input
						name="name"
						placeholder="Find Breed"
						onChange={(e) => {
							handleChange(e);
						}}
					/>
				</div>

				{/* [ ] Botones/Opciones para filtrar por: Raza existente*/}
				<div className="Order-By-Breed">
					<select
						name="breed"
						onChange={(e) => this.handleChange(e)}
					>
						<option className="Breed" value="">
							{" "}
							All Breed
						</option>
						{breeds.map((e, i) => (
							<option key={i} value={e}>
								{e}
							</option>
						))}
					</select>
				</div>

				{/* Botones/Opciones para filtrar por:Temperamento*/}
				<div className="Order-By-Temperament">
					<select
						name="temperament"
						onChange={(e) => handleChange(e)}
					>
						<option className="Temperament" value="">
							{" "}
							All Temperaments{" "}
						</option>
						{temps.map((e, i) => (
							<option key={i} value={e.name}>
								{e.name}
							</option>
						))}
					</select>
				</div>

				{/* [ ] Botones/Opciones para ordenar tanto asc como desc las razas de perro por:
				Orden alfabético  /  Peso */}

				<div className="Order-By-Name">
					<select
						name="sort"
						onChange={(e) => this.handleChange(e)}
					>
						<option value=""> Order By</option>
						<option value="nameAsc">Name A to Z</option>
						<option value="nameDesc">Name Z to A</option>
						<option value="weightAsc">Asc weight</option>
						<option value="weightDsc">Desc weight</option>
					</select>
				</div>
			</div>
			<Pagination
				dogsByPage={pag.dogsByPage}
				allDogs={allDogs.length}
				Paginate={paginate}
			/>
			<Cards dogs={currentDogs} />
		</div>
	);
};

export default HomePage;
